import FreeDom from 'components/freedom'
import {mapGetters} from 'vuex'
import { RadioGroup, Radio, Cell, CellGroup,SubmitBar,Field  } from 'vant'
import Storage from 'good-storage'
import {pay,codepay,getUserInfo} from 'api'
import wx from 'weixin-js-sdk'
export default {
    components: {
        FreeDom,
        'van-radio-group':RadioGroup,
        'van-radio':Radio,
        'van-cell-group': CellGroup,
        'van-cell':Cell,
        'van-submit-bar':SubmitBar,
        'van-field': Field
    },
    data () {
        return {
            radio: '1',
            value: ''
        }
    },
    computed:{
        ...mapGetters(['userInfo','identityCode','token'])
    },
    methods: {
        getUserInfo(data){
            getUserInfo(data).then(res =>{
                console.log(res)
                if(res.code === 0){
                    this.$store.commit('SET_USER',res.result.data)
                    this.$store.commit('SET_IDENTITYCODE',res.result.data.type)
                }
            })
        },
        getPay(data){
            let that = this
            pay(data).then(res =>{   
                if(res.code === 0){
                    var data = res.jsapi
                    let appId = data.appId;
                    let timestamp = data.timeStamp;
                    let nonceStr = data.nonceStr;
                    let signature = data.signature;
                    let packages = data.package;
                    let paySign = data.paySign;
                    wx.config({
                        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                        url: 'http://wx.wxrwin.com/#/buysvip',
                        appId: appId, // 必填，公众号的唯一标识
                        timestamp: timestamp, // 必填，生成签名的时间戳
                        nonceStr: nonceStr, // 必填，生成签名的随机串
                        signature: signature, // 必填，签名，见附录1
                        jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
                    });
                    wx.ready(function(){
                        wx.chooseWXPay({
                            timestamp: timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                            nonceStr: nonceStr, // 支付签名随机串，不长于 32 位
                            package: packages, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                            signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                            paySign: paySign, // 支付签名
                            success: function(res) {
                                that.$store.commit('SET_IDENTITYCODE',2)
                                let data = {token:that,token}
                                that.getUserInfo(data)
                                let type = this.$route.query.type
                                if(type === 'details'){
                                    that.$router.replace(Storage.session.get('detailPath'))
                                }else{
                                    that.$router.replace('/svip')
                                }
                            },
                            fail:function(res){
                                console.log(res)
                            }
                        });
                    });
                    wx.error(function(res) {
                        // config信息验证失败会执行error函数，如签名过期导致验证失败，具体错误信息可以打开config的debug模式查看，也可以在返回的res参数中查看，对于SPA可以在这里更新签名。
                        /*alert("config信息验证失败");*/
                        console.log(res)
                    });
                }
            })
        },
        getCodepay(data){
            let that = this
            codepay(data).then(res =>{
                if(res.code === 0){
                    that.$store.commit('SET_IDENTITYCODE',2)
                    let data = {token:that,token}
                    that.getUserInfo(data)
                    let type = this.$route.query.type
                    if(type === 'details'){
                        that.$router.replace(Storage.session.get('detailPath'))
                    }else{
                        that.$router.replace('/svip')
                    }
                }else{
                    that.$Toast(res.result.msg)
                }
            })
        },
        onSubmitPay(e){
            let data = {
                token: this.token,
                appid: 'wx87b3acb00474dd24',
                money: '0.01'
            }
            this.getPay(data)
        },
        onSubmitCodepay(e){
            if(this.value == ''){
                this.$Toast('请输入正确的智淘码')
                return
            }
            let data = { code: this.value, uid: this.userInfo.uid,token: this.token}
            this.getCodepay(data)
        }
    },
    mounted () {
        let type = this.$route.query.type
        console.log(Storage.session.get('detailPath'))
    }
}



